<template>
  <div class="nav" id="nav">
    <div class="content">
      <div class="logo">
        Zber
      </div>
      <ul class="menu">
        <li>{{text.us}}</li>
        <li>{{text.pro}}</li>
        <li>{{text.news}}</li>
        <li>{{text.cop}}</li>
        <li>{{text.cus}}</li>
        <li>{{text.int}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Nav',
    data() {
      return {
        zh: {
          us: '我们',
          pro: '产品',
          news: '要闻',
          cop: '合作',
          cus: '客户',
          int: '互动'
        },
        en: {
          us: 'About us',
          pro: 'Products',
          news: 'News',
          cop: 'Cooperation',
          cus: 'Customers',
          int: 'Interaction'
        }
      }
    },
    props: {
      lang: String
    },
    computed: {
      text() {
        return this.lang === 'en' ? this.en : this.zh
      }
    }
  }
</script>

<style lang="scss" scoped>
  .nav {
    position: fixed;
    width: 100%;
    background: rgba(0, 0, 0, .1);
    transition: background-color .4s;
    z-index: 1;

    &.fixed {
      background: #fff;
      border-bottom: 1px solid #aaa;

      .menu {
        li {
          color: #333;
        }
      }
    }

    .content {
      width: 960px;
      margin: 0 auto;
      display: flex;
      align-items: center;
      height: 75px;
    }

    .logo {
      width: 256px;
      height: 64px;
      line-height: 64px;

      font-size: 32px;
      font-style: italic;
      font-weight: 700;
      text-decoration: initial;
      color: #12b7f5;
    }

    .menu {
      flex: auto;
      display: flex;
      justify-content: flex-end;

      li {
        float: left;
        padding: 10px;
        margin: 0 10px;
        text-align: center;
        font-size: 16px;
        color: #fff;
        cursor: pointer;
      }
    }
  }
</style>



