<template>
  <div id="app">
    <Nav :lang="lang" />
    <div id="page1"></div>

    <img v-if="zh" src="./assets/page1-bottom.jpg" alt="page1">
    <img v-if="en" src="./assets/en/page1-bottom-en.jpg" alt="page1">

    <div id="page2" class="page"></div>

    <img v-if="zh" src="./assets/page2-bottom.jpg" alt="page2">
    <img v-if="en" src="./assets/en/page2-bottom-en.jpg" alt="page2">

    <div id="page3" class="page"></div>

    <img v-if="zh" src="./assets/page3-bottom.jpg" alt="page3">
    <img v-if="en" src="./assets/en/page3-bottom-en.jpg" alt="page3">

    <div id="page4" class="page"></div>

    <img v-if="zh" src="./assets/user.jpg" alt="user">
    <img v-if="en" src="./assets/en/user-en.jpg" alt="user">

    <img v-if="zh" src="./assets/connect.jpg" alt="connect">
    <img v-if="en" src="./assets/en/connect-en.jpg" alt="connect">

    <img v-if="zh" src="./assets/bottom.jpg" alt="bottom">
    <img v-if="en" src="./assets/en/bottom-en.jpg" alt="bottom">
  </div>
</template>

<script>
import $ from 'jquery'
import Particleground from 'Particleground.js';
import Nav from './Nav'

export default {
  name: 'app',
  components: {
    Nav
  },
  data() {
    return {
      lang: 'zh'
    }
  },
  computed: {
    zh() {
      return this.lang === 'zh'
    },
    en() {
      return this.lang === 'en'
    }
  },
  beforeMount() {
    const lang = getLang()
    this.lang = lang
  },
  mounted() {
    new Particleground.particle('#page1')

    const nav = $('#nav')

    const page1 = $('#page1')
    const page1H = page1.height() - 75

    const page2 = $('#page2')
    const page3 = $('#page3')
    const page4 = $('#page4')

    $(window).scroll(e => {
      const top = document.documentElement.scrollTop
      console.log(top)

      // nav
      if (top >= page1H) {
        nav.addClass('fixed')
      } else {
        nav.removeClass('fixed')
      }

      // page2
      changePosition(page2, 400)
      changePosition(page3, 1600)
      changePosition(page4, 2940)
    })
  }
}

function getLang() {
  const search = location.search
  const match = search.match(/.*lang=([a-z]*)\&?/)

  if (match) {
    return match[1]
  }

  return 'zh'
}

function changePosition(el, offset) {
  const top = document.documentElement.scrollTop

  if (top >= offset) {
    el.css({
      backgroundPosition: `50% ${-1080 + top - offset}px`
    })
  } else {
    el.css({
      backgroundPosition: `50% -1080px`
    })
  }
}
</script>

<style lang="scss">
  @import url('./styles/reset.scss');
  body {
    overflow-x: hidden;
    height: 100vh;
  }

  #app {
    width: 100vw;
  }

  .page {
    background-position: 50% -1080px;
    background-repeat: no-repeat;
    width: 100vw;
    height: 800px;
  }

  #page1 {
    position: relative;
    background: url(./assets/page1-content.jpg) no-repeat;
    background-size: 100% 100%;
    width: 100vw;
    height: 100vh;
  }

  #page2 {
    background-image: url(./assets/page2-content.jpg);
  }

  #page3 {
    background-image: url(./assets/page3-content.jpg);
  }

  #page4 {
    background-image: url(./assets/page4-content.jpg);
  }
</style>



